@import url('https://fonts.googleapis.com/css2?family=Gabarito:wght@400..900&family=Jura:wght@300..700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lora:ital,wght@0,400..700;1,400..700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Zilla+Slab:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap');

/* #region COLORS */
:root {
  --iblack: #00010D;
  --icement: #3B403F;
  --ichia: #8c8581;
  --igold: #dac0a3;
  --itan: #bfb8b4;
  --ibeige: #d9d2d0;
  --iespresso: #6f4e37;
}

.blackque {
  color: #00010D;
}

.espresso {
  color: #6f4e37;
}

.cement {
  color: #3B403F;
}

.chia {
  color: #8C8581;
}

.gold {
  color: #DAC0A3;
}

.tan {
  color: #BFB8B4;
}

.beige {
  color: #D9D2D0;
}

/* #endregion */

body {
  margin: 0;
  font-family: "Gabarito", sans-serif;
  background: var(--iblack);
  color: var(--ibeige);
  min-width: 100px;
}

hr {
  border-color: var(--ichia);
}

@starting-style {

  .review *,
  .gallery * {
    opacity: 0;
  }
}

.router {
  margin-top: 105px;
  min-width: 100px;
}

.content>* {
  padding: 20px;
}

.glass {
  background: rgba(50, 55, 54, 0.3);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 10px;
}

.quote {
  font-family: "Lora", serif;
  font-weight: 400;
}

.section {
  width: auto;
  /* border: 1px #fff5 solid; */
  border-radius: 10px;
  padding: 20px;
}

.review *,
.gallery * {
  transition: opacity 1s;
}

.thin {
  font-weight: 200;
}

/* #region BUTTONS */
.btn-primary {
  padding: 20px 26px;
  background: var(--igold);
  border: 2px var(--igold) solid;
  color: var(--iblack);
  border-radius: 30px;
  font-family: inherit;
  font-size: 1em;
  cursor: pointer;
  transition-duration: 0.2s;
}

.btn-primary:hover {
  color: var(--igold);
  background: var(--iblack);
  box-shadow: 0 4px 10px 0 rgba(210, 210, 210, 0.3), 0 6px 20px 0 rgba(210, 210, 210, 0.29);
}

.btn-outline {
  padding: 20px 26px;
  background: none;
  border: 2px var(--ibeige) solid;
  color: var(--ibeige);
  border-radius: 30px;
  font-family: inherit;
  font-size: 1em;
  cursor: pointer;
  transition-duration: 0.2s;
}

.btn-outline:hover {
  border-color: var(--icement);
  color: var(--itan);
}

/* #endregion */

/* #region NAVBAR */

.navbar {
  position: fixed;
  left: 20px;
  right: 20px;
  top: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 1000;
}

.navbar>* {
  padding-inline: 20px;
}

.logo {
  width: 60px;
  height: 60px;
}

.navbar-div-title {
  display: flex;
  align-items: center;
  gap: 20px;
}

.navbar-div-title>* {
  text-shadow: 0px 0px 8px var(--icement);
}

.navbar h1 {
  font-size: 30pt;
}

.navbar h1:hover {
  color: white;
  text-shadow: 4px 4px var(--ichia);
}

.navbar a {
  text-decoration: none;
  color: #D9D2D0;
}

.navbar nav {
  display: inline-flex;
  gap: 20px;
}

.navbar nav a {
  padding: 10px;
  border-bottom: 2px transparent solid;
  transition-duration: 0.2s;
  width: max-content;
}

.navbar nav a:hover {
  color: white;
  border-bottom: 2px solid #DAC0A3;
  background-color: #3B403F;
  border-radius: 10px;
}

/* #endregion */

/* #region HOME */

/* .home .banner {
  height: 400px;
  display: flex;
  padding-inline: 80px;
  align-items: center;
  justify-content: space-evenly;
} */

.gallery {
  display: flex;
  overflow: scroll;
  scrollbar-width: 0;
  scroll-behavior: smooth;
}

.gallery>* {
  margin-right: 20px;
}

.card {
  width: fit-content;
  border-radius: 10px;
  text-align: center;
  position: relative;
}

.card img {
  width: min(22vw, 300px);
  height: inherit;
  border-radius: 10px;
  cursor: pointer;
  transition: opacity .2s ease, filter .2s ease;
  object-fit: contain;
  border: 1px solid var(--icement);
}

.card img:hover {
  opacity: 20%;
  filter: blur(10px);
}

.card-details {
  position: absolute;
  width: 100%;
  bottom: 20px;
  pointer-events: none;
  opacity: 0;
  font-size: min(1.2vw, 14px);
  transition-duration: 0.2s;
}

.card img:hover+.card-details {
  opacity: 1;
}

.card-details>* {
  margin: 10px;
}

/* #endregion */

/* #region REVIEW */

.review>* {
  margin-top: 40px;
  margin-bottom: 20px;
}

.review-banner {
  display: flex;
  gap: 20px;
  justify-content: center;
}

.review-banner img {
  width: 300px;
  border-radius: 10px;
  aspect-ratio: 1;
}

.review-info {
  display: flex;
  flex-direction: column;
  justify-content: end;
}

.review-info h1 {
  font-size: 55pt;
  margin: inherit;
}

.review-info h2 {
  font-size: 28pt;
  margin: inherit;
}

.review-info h3 {
  font-size: 24pt;
  margin: inherit;
}

.review-info>span {
  display: flex;
  align-items: center;
  gap: 20px;
  padding-top: 10px;
}

.review-content {
  padding: 0px min(20%, 300px);
  text-align: center;
}

.quote {
  font-size: 30pt;
  font-style: oblique;
}

@media only screen and (max-width: 800px) {
  .review-banner {
    display: block;
    text-align: center;
  }

  .review-banner span {
    justify-content: center;
  }

  .review-content {
    padding: 0px 5%;
  }

  .review-info h1 {
    margin-top: 10px;
  }

  .quote {
    font-size: 20pt;
  }
}

.review-text {
  margin-top: 60px;
  text-align: justify;
  font-size: 18px;
  font-family: "Zilla Slab", serif;
  font-weight: 200;
  line-break: auto;
  text-indent: 50px;
  line-height: 1.6;
  letter-spacing: .6px;
}

/* #endregion */

.instructions {}

.instructions li {
  display: flex;
  align-items: center;
  gap: 20px;
}

.not-found {
  display: flex;
  flex-direction: column;
  text-align: center;
  gap: 20px;
  height: 400px;
}

.not-found * {
  text-decoration: none;
}

/* #region COFFEERATING */

.bean {
  font-size: 30px;
  text-align: left;
  font-style: normal;
  display: inline-block;
  position: relative;
  unicode-bidi: bidi-override;
}

/* #endregion */

.loading {
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  gap: 20px;
}

.loading>* {
  margin: 0;
}

.loader {
  width: 50px;
  aspect-ratio: 1;
  border-radius: 50%;
  border: 8px solid var(--ichia);
  border-color: var(--ichia) var(--iblack);
  animation: l1 1s infinite;
}

@keyframes l1 {
  to {
    transform: rotate(.5turn)
  }
}

.centered {
  width: 100%;
  height: 100%;
}


@media only screen and (max-width: 740px) {
  .navbar-div-title p {
    display: none;
  }
}

@media only screen and (max-width: 700px) {
  .card img {
    width: 150px;
  }

  .card-details {
    font-size: 10px;
  }

  .card-details p.chia {
    display: none;
  }
}

@media only screen and (max-width: 560px) {
  .navbar h1 {
    font-size: 7vw;
  }

  .navbar h1 {
    display: none;
  }

  .navbar {
    padding-block: 19px;
  }
}